

















import Vue from 'vue';
import {Component, Prop} from 'vue-property-decorator';

@Component
export default class Tags extends Vue {

  @Prop() dataSource: Tag[] | undefined;

  selectedTags: string[] = [];

  addTag() {
    const name = window.prompt('')!.trim();
    if (!name) return window.alert('');
    if (name.length > 4) return window.alert('');
    if (this.dataSource) {
      const names = this.dataSource.map(item => item.name);
      if (names.indexOf(name) >= 0) return window.alert('');
      this.$store.commit('createTag', name);
    }
  }
  selectTag(tag: string) {
    const id = this.selectedTags.indexOf(tag);
    if (id >= 0) {
      this.selectedTags.splice(id, 1);
    } else {
      this.selectedTags.push(tag);
    }
    this.$emit('update:selectedTag', this.selectedTags);
  }
}
