<template>
    <div class="delete">
      <div class="slider">
        <div class="content"
             @touchstart='touchStart'
             @touchmove='touchMove'
             @touchend='touchEnd'
             :style="deleteSlider"
        >
          <!--          -->
          <slot></slot>
        </div>
        <div class="remove" ref='remove'  @click="deleteLine">
          
        </div>
      </div>

    </div>
  </template>
<script >
  export default {
    name: 'Delete',
    props:['id'],
    data() {
      return {
        startX:0,   //
        endX:0,     //
        moveX: 0,   //
        disX: 0,    //
        deleteSlider: '',//,v-bind:style="deleteSlider"
      }

    },
    methods:{
      mounted(){
        console.log(123)
        console.log(this.id)
      },
      deleteLine (){
        this.deleteSlider = "transform:translateX(0px)";
        this.$emit('deleteLine',this.id);
      },
      touchStart(ev){
        ev= ev || event
        //tounches1

        if(ev.touches.length == 1){
          // 
          this.startX = ev.touches[0].clientX;
        }
      },
      touchMove(ev){
        ev = ev || event;
        //
        const wd=this.$refs.remove.offsetWidth;
        if(ev.touches.length == 1) {
          // 
          this.moveX = ev.touches[0].clientX

          // 
          this.disX = this.startX - this.moveX;
          // console.log(this.disX)
          // 
          if(this.disX < 0 || this.disX == 0) {
            this.deleteSlider = "transform:translateX(0px)";
            // 0
          }else if (this.disX > 0) {
            // *5
            this.deleteSlider = "transform:translateX(-" + this.disX*5 + "px)";

            // 
            if (this.disX*5 >=wd) {
              this.deleteSlider = "transform:translateX(-" +wd+ "px)";

            }
          }
        }
      },
      touchEnd(ev){
        ev = ev || event;
        const wd=this.$refs.remove.offsetWidth;
        if (ev.changedTouches.length == 1) {
          const endX = ev.changedTouches[0].clientX;

          this.disX = this.startX - endX;
          // console.log(this.disX)
          //,

          if ((this.disX*5) < (wd/2)) {

            this.deleteSlider = "transform:translateX(0px)";
          }else{
            // 
            this.deleteSlider = "transform:translateX(-"+wd+ "px)";
          }
        }
      }
    }

  };
</script>

<style lang='scss' scoped>
  .slider{
    width: 100%;
    height:40px;
    position: relative;
    user-select: none;
    .content{
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      z-index: 100;
      //    
      transition: 0.3s;

    }
    .remove{
      position: absolute;
      right: 0;
      top: 0;
      background: #fef0f0;
      color: #f56c6c;
      height: 40px;
      line-height: 40px;
      width: 60px;
      text-align: center;
    }
  }
</style>